.often .often-content {
  width: 853px;
  height: 327px;
  box-shadow: 0px 10px 24px 0px #f4f4f4;
  border: 1px solid rgba(0, 0, 0, 0.06);
  margin-bottom: 24px;
  padding: 0 39px;
}
.often .often-content .title {
  width: 100%;
  padding: 24px 0;
  border-bottom: 1px solid #F0F0F0;
  font-size: 24px;
  font-weight: 500;
}
.often .often-content .form {
  margin-top: 17px;
}
.often .often-table {
  width: 853px;
  box-shadow: 0px 10px 24px 0px #f4f4f4;
  border: 1px solid rgba(0, 0, 0, 0.06);
  padding: 24px 40px;
}
.often .often-table .qu-btn {
  color: #0091ff;
  cursor: pointer;
}
@media only screen and (max-width: 768px) {
  .often .often-content {
    width: 100%;
  }
  .often .often-table {
    width: 100%;
    padding: 10px 20px;
  }
}
